<template>
  <div>
    <v-toolbar dark prominent app bg class="navnav">
      <router-link to="/">
        <v-app-bar-nav-icon><v-icon>mdi-home</v-icon></v-app-bar-nav-icon>
      </router-link>
      <!-- <v-toolbar-title id="logo">GR Whatsapp</v-toolbar-title> -->

      <v-spacer></v-spacer>

      <div class="mt-2">
        <v-btn
          text
          fab
          @click="darkModeToggle"
          v-bind="attrs"
          v-on="on"
          height="0"
        >
          <v-icon>{{ icon }}</v-icon>
        </v-btn>
        <router-link to="/addpost"
          ><v-btn text x-small><v-icon>mdi-upload</v-icon></v-btn></router-link
        >
        <span v-if="!$store.getters.isLoggedIn">
          <router-link to="/auth/login"
            ><v-btn color="" x-small text
              ><span class="mdi mdi-login"> دخول</span></v-btn
            ></router-link
          >
          <span>
            <v-btn color="" class="mr-1" x-small text to="/auth/register"
              ><span class="mdi mdi-plus"> تسجيل</span></v-btn
            >
          </span>
        </span>
        <span v-else>
          <accounts />
        </span>
      </div>
    </v-toolbar>
    <div style="height:40px"></div>
  </div>
</template>
<script>
import accounts from "./accounts";
export default {
  components: {
    accounts,
  },
  data() {
    return {
      darkMode: false,
      icon: "",
      on: "",
      attrs: "",
      drawer: false,
      group: null,
    };
  },
  methods: {
    enableDarkMode() {
      this.$vuetify.theme.dark = true;
      this.icon = "mdi-white-balance-sunny";
      localStorage.setItem("dark", "true");
    },
    disableDarkMode() {
      this.$vuetify.theme.dark = false;
      this.icon = "mdi-weather-night";
      localStorage.setItem("dark", "false");
    },
    darkModeToggle() {
      this.darkMode = localStorage.getItem("dark");
      if (this.darkMode !== "true") {
        this.enableDarkMode();
      } else {
        this.disableDarkMode();
      }
    },
  },
  mounted() {
    this.darkMode = localStorage.getItem("dark");
    if (this.darkMode === "true") {
      this.enableDarkMode();
    } else this.disableDarkMode();
  },
};
</script>
<style>
.navnav {
  background-image: linear-gradient(
    rgb(30, 75, 38),
    rgb(60, 136, 81),
    rgb(22, 148, 60)
  );
}
</style>
